/* =========================================
   Amar Mahallah — src/App.css (FULL FILE)
   Palette from Flutter + iOS fullscreen handling
   - Subtitle: deeper complementary color (--subtitle)
   - Mobile logo size: 250×250 px
   ========================================= */

/* Brand tokens */
:root{
  --app:#00B069;      /* am_theme_color */
  --accent:#3A9ECC;   /* am_blue */
  --bg:#FFFFFF;       /* backgroundcolor */
  --bg2:#CDFFEC;      /* backgrColor */
  --text:#0F2A22;     /* deep teal text */
  --subtitle:#124C43; /* NEW: deep complement for subtitle */
  --vh:1vh;           /* JS hook fallback */
}

/* Fill + paint gradient on html/body for iOS notch area */
*{ box-sizing:border-box; }
html{
  height:-webkit-fill-available;
  background:
    radial-gradient(120% 80% at 60% -10%, rgba(58,158,204,0.22) 0%, transparent 50%),
    linear-gradient(180deg, var(--bg) 0%, var(--bg2) 100%);
  background-attachment:fixed;
}
body, #root{
  min-height:100dvh; min-height:100lvh; min-height:100svh;
  min-height:-webkit-fill-available; height:-webkit-fill-available;
}
body{
  margin:0; color:var(--text);
  -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale;
  background:
    radial-gradient(120% 80% at 60% -10%, rgba(58,158,204,0.22) 0%, transparent 50%),
    linear-gradient(180deg, var(--bg) 0%, var(--bg2) 100%);
  background-attachment:fixed;
  overscroll-behavior-y: contain;
}

/* Backstop layer to guarantee full paint */
body::before{
  content:"";
  position:fixed; inset:0; z-index:-1; pointer-events:none;
  background:
    radial-gradient(120% 80% at 60% -10%, rgba(58,158,204,0.22) 0%, transparent 50%),
    linear-gradient(180deg, var(--bg) 0%, var(--bg2) 100%);
}

/* Reduced motion */
@media (prefers-reduced-motion:reduce){
  *{ animation:none!important; transition:none!important; scroll-behavior:auto!important; }
}

/* =========================
   HERO (layout shell)
   ========================= */
.hero{
  min-height:100dvh; min-height:100lvh; min-height:100svh;
  min-height:-webkit-fill-available; min-height:calc(var(--vh)*100);
  display:grid; align-items:center;
  padding:28px 20px 40px; background:transparent;
}
@supports (padding:max(0px)){
  .hero{
    padding-top:max(28px, env(safe-area-inset-top));
    padding-right:max(20px, env(safe-area-inset-right));
    padding-bottom:max(40px, env(safe-area-inset-bottom));
    padding-left:max(20px, env(safe-area-inset-left));
  }
}
.hero__inner{
  width:100%; max-width:1280px; margin:0 auto;
  display:grid; gap:28px;
}
@media (min-width:900px){
  .hero__inner{
    grid-template-columns:1.3fr .7fr;
    gap:clamp(32px,4vw,64px); align-items:center;
  }
}

/* Tablet tuning */
@media (min-width:768px) and (max-width:1024px){
  .hero{ align-items:flex-start; padding-top:18px; }
  .hero__inner{ gap:20px; }
}

/* ==============================
   LEFT: logo + copy + CTAs
   ============================== */
.brandbar{ display:flex; align-items:center; justify-content:center; margin-bottom:12px; }

/* Square logo: mobile = 250×250 (as requested) */
.logo{
  width:250px; height:250px; object-fit:contain; border-radius:24px;
  box-shadow:0 12px 32px rgba(0,176,105,0.18);
}
/* Ultra-small phones */
@media (max-width:360px){ .logo{ width:220px; height:220px; } }
/* Align left on wider screens */
@media (min-width:900px){ .brandbar{ justify-content:flex-start; } }
/* Large desktops gentle upscale */
@media (min-width:1100px){ .logo{ width:280px; height:280px; } }

.title{
  margin:0 0 8px; text-align:center;
  font-size:clamp(24px,4.2vw,42px); line-height:1.15; color:#0D382D;
}
.subtitle{
  margin:0 0 18px; text-align:center;
  color:var(--subtitle);              /* deep complementary tone */
  font-size:clamp(14px,2.2vw,16px);
}
@media (min-width:900px){ .title,.subtitle{ text-align:left; } }

/* =========================
   Actions: Store badges + Vendor
   ========================= */
.actions{
  display:flex; flex-direction:column; align-items:center; justify-content:center;
  gap:10px; width:100%; max-width:680px; margin:4vh auto 0;
}
@media (min-width:768px){
  .actions{ flex-direction:row; align-items:center; justify-content:flex-start; gap:10px; margin:0; }
}

/* Badge-style CTA buttons (Play, App Store, Vendor) */
.store-btn,
.vendor-btn{
  display:inline-block;
  padding:0;
  background:transparent;
  border:none;
  line-height:0;
}
.store-btn:hover,
.vendor-btn:hover{
  opacity:.96;
}
.store-btn:focus-visible,
.vendor-btn:focus-visible{
  outline:3px solid var(--accent);
  outline-offset:4px;
}

/* Images inside badges */
.store-img,
.vendor-btn img{
  display:block;
  height:56px;
  width:auto;
}
@media (min-width:768px){
  .store-img,
  .vendor-btn img{
    height:52px;
  }
}

/* =========================
   RIGHT: phone mockup (PNG)
   ========================= */
.visual{ display:grid; place-items:center; }
.mock{
  width:min(420px, 88vw); height:auto;
  filter: drop-shadow(0 20px 60px rgba(0,176,105,.18))
          drop-shadow(0 8px 18px rgba(58,158,204,.14));
}

/* =========================
   Footer (minimal)
   ========================= */
.footer{ margin-top:24px; font-size:13px; color:#4b7f76; text-align:center; }
@media (min-width:900px){ .footer{ text-align:left; } }

/* Text wrap safety */
.title,.subtitle{ word-break:break-word; overflow-wrap:anywhere; }
